@import './normalize.css';

:root {
  --color-dark: #161400;
  --color-light: #eee0db;
  --color-accent: #ff8412;
  --color-bg-info: rgba(0, 0, 0, .3);
  --size-btn: 2em;
  --spacing-btn: .2em;
  --spacing-default: 2em;
  --spacing-huge: 5em;
}

/*
    defaults
*/
html {
    background-color: var(--color-dark);
    color: var(--color-light);
    font-family: 'Roboto', sans-serif;
    font-size: 22px;
}
body {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}
h1 {
    font-family: 'Oswald', sans-serif;
    letter-spacing: normal;
}

a { color: var(--color-light); }
a:focus, a:hover, a:active { color: var(--color-accent); }

/*
    layout
*/
main {
    height: 100vh;
    display: grid;
    grid-template-columns: auto 1fr;
    grid-template-rows: auto;
}
section {
    padding: var(--spacing-default);
}
.info {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.content {
    grid-column: 2 / 3;
    grid-row: 1 / 2;
}
.links a {
    font-size: smaller;
    padding: .3em 1em;
    padding-left: 2em;
    text-decoration: none;
    margin: 0 0 .618em .618em;
    background-repeat: no-repeat;
    background-position: left center;
    background-size: contain;
    clear: right;
    float: right;
}
#db {
    background-image: url('https://faforever.github.io/spooky-db/apple-touch-icon-precomposed.png');
}
#utt {
    background-image: url('https://spooky.github.io/uri-template-tester/icon.png');
}
#dp {
    background-image: url('https://spooky.github.io/duel-pig/favicon.png');
}
#cd {
    background-image: url('https://spooky.github.io/countdown/icon.png');
    background-position-y: 1px;
}
@media screen and (max-width: 840px) {
    html {
        min-width: 300px;
        overflow: auto;
    }
    main {
        grid-template-columns: auto;
        grid-template-rows: auto 1fr;
    }
    .info {
        grid-column: 1 / 2;
        grid-row: 1 / 2;
    }
    .content {
        grid-column: 1 / 2;
        grid-row: 2 / 3;
    }
}

/*
    components
*/
.avatar {
    display: inline-block;
    width: 1em;
    height: 1em;
    border-radius: 50%;
    vertical-align: bottom;
    margin-right: .3em;
    border: 2px solid var(--color-accent);
}
.punchline {
    letter-spacing: .5px;
    font-variant: small-caps;
    margin-top: -1em;
    color: var(--color-accent);
}
.info {
    background-color: var(--color-bg-info);
    text-align: right;
    padding-top: var(--spacing-huge);
}
.image-meta {
    font-size: smaller;
    position: fixed;
    bottom: var(--spacing-default);
    right: var(--spacing-default);
    text-align: right;
}
.image-meta p {
    font-style: italic;
    margin-bottom: .3em;
}
.image-meta a {
    text-decoration: none;
    color: var(--color-light);
}
.image-meta a:hover, .image-meta a:focus {
    color: var(--color-accent);
}
.image-meta span {
    font-variant: small-caps;
}
#next {
    box-sizing: border-box;
    display: inline-block;
    width: var(--size-btn);
    height: var(--size-btn);
    vertical-align: baseline;
    text-align: center;
    float: right;
    background: none;
    border: none;
    color: var(--color-light);
    text-shadow: 0 0 3px var(--color-accent);
    box-shadow: 0 0 12px var(--color-light);
    transition: box-shadow .15s;
    margin: 0;
    padding: var(--spacing-btn);
    border-radius: 50%;
}
#next:hover {
    cursor: pointer;
}
#next:hover, #next:active {
    box-shadow: 0 0 9px var(--color-light);
    margin: 0;
    padding: var(--spacing-btn);
}
